import React, { Component } from 'react'
import { Checkbox, Alert } from 'antd';
import { connect } from 'dva';
import style from './login.module.css';
import Login from '@/components/Login';

const { UserName, Password, Submit } = Login;

const mapState = store => {
  window.store = store;
  return { ...store.login }
}

@connect(mapState)
class LoginDemo extends Component {
  state = {
    notice: '',
    autoLogin: false,
    user_name: '',
    user_pwd: ''
  }
  formLogin(error, values) {
    if (error !== null) return;
    let { user_name, user_pwd } = values;
    sessionStorage.setItem('userInfo', JSON.stringify({ user_name, user_pwd }));
    this.props.dispatch({ type: 'login/login', user_name, user_pwd });
  }
  changeAutoLogin = ev => {
    this.setState({ autoLogin: ev.target.checked }, () => {
      if (this.state.autoLogin) {
        let userInfo = JSON.parse(sessionStorage.getItem('userInfo'));
        if (!userInfo) {
          return;
        }
        let { user_name, user_pwd } = userInfo;
        this.setState({ user_name, user_pwd })
      } else {
        this.setState({ user_name: '', user_pwd: '' })
      }
    });
  }
  render() {
    return <div className={style.wraper}>
      <Login
        className={style['login-wraper']}
        defaultActiveKey={this.state.type}
        onTabChange={this.onTabChange}
        onSubmit={(error, values) => {
          this.formLogin(error, values);
        }}
      >
        <div>
          {
            this.state.notice &&
            <Alert style={{ marginBottom: 24 }} message={this.state.notice} type="error" showIcon closable />
          }
          <UserName name="user_name" />
          <Password name="user_pwd" />
        </div>
        <div className='ant-form-item'>
          <Checkbox checked={this.state.autoLogin} onChange={ev => this.changeAutoLogin(ev)}></Checkbox>
          <a href='javasript:void(0)' style={{ float: 'right' }}></a>
        </div>
        <Submit style={{ width: '100%' }}></Submit>
      </Login>
    </div>
  }
}
export default LoginDemo;